	    width: 50%;
    display: block;
    float: left;
    text-align: left;
}
div.nav-links div.nav-next{
	    width: 50%;
    display: block;
    float: right;
    text-align: right;
}
div.header-menu-widgets ul.menu li a:hover{
	background: #ffffff;
}
