.yellow-icon {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #ffc600;
    transition: background-color .25s ease-out,color .25s ease-out;
    position: relative
}

.yellow-icon img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto
}

.yellow-icon:hover {
    background-color: smart-scale(#ffc600,10%,25%)
}

